import Dexie from "dexie";
let db = new Dexie("PlayersDatabase");
db.version(1).stores({
  players: "++id, name",
});
export const createPlayer = async (player) => {
  await db.players.add(player);
};

export const getPlayer = async (name) => {
  let player = await db.players.get({ name: name });
  console.log(player);
};

export const checkValidName = () => {
  let input = document.querySelector("input");
  let name = input.value;
  input.removeEventListener("input", removeErrorClass);

  if (name.length < 3) {
    let span = document.createElement("span");
    span.classList.add("span-error");
    span.innerText = "El nombre debe contener al menos 3 caracteres";
    input.after(span);
    input.value = "";
    input.classList.add("error");
    input.addEventListener("input", removeErrorClass);
    return false;
  }
  createPlayer({ name, score: 0 });
};

const removeErrorClass = (e) => {
  if (e.target.value !== "") {
    if (document.querySelectorAll(".span-error").length > 0) {
      let span = document.querySelector(".span-error");
      span.remove();
    }

    e.target.classList.remove("error");
  }
};
